(function(app){
    app.controller('SignUpCtrl', function($scope, Modal, $state, Authentication, SessionStore, UtilityService, Idle){

        var init = function(){
            $scope.forgotPasswordLink = UtilityService.getAppConstant('FORGOT_PASSWORD_LINK');
            Idle.unwatch();
        }

        var saveParametersInSessionStore = function(loginResponseData){
            if(loginResponseData != undefined){
                var currentVaultKey = loginResponseData.client_token;
                var isAdmin = loginResponseData.admin.toLowerCase() != 'no';
                var accessSafes = loginResponseData.access;
                var policies = loginResponseData.policies;
                SessionStore.setItem("myVaultKey",currentVaultKey);
                SessionStore.setItem("isAdmin",isAdmin);
                SessionStore.setItem("accessSafes", JSON.stringify(accessSafes));
                SessionStore.setItem("policies",policies);
                $state.go('safes');
            }
        }
        var error = function (size) {
            Modal.createModal(size, 'error.html', 'SignUpCtrl', $scope);
        };

        $scope.close = function () {
            Modal.close();
        };

        $scope.login = function() {
          $scope.isLoadingData = true;
          var username  = $scope.username.toLowerCase();
          username = Authentication.formatUsernameWithoutDomain(username);
          var reqObjtobeSent = {"username":username,"password":$scope.password};
          Authentication.authenticateUser(reqObjtobeSent).then(function(response){
              $scope.isLoadingData = false;
              if(UtilityService.ifAPIRequestSuccessful(response)){
                  saveParametersInSessionStore(response.data);
              } else if (response.data && response.data.errors){
                var errors = response.data.errors;
                return Modal.createModalWithController('error.html', {
                  shortMessage: errors[0] || 'There was an error. Please try again, if the problem persists contact an administrator',
                  longMessage: errors[1]
                  });
              } else {
                return Modal.createModalWithController('error.html', {
                  shortMessage: 'Something went wrong, please try again later.'
                })
              }
          })
        };

        init();
    })
})(angular.module('vault.features.SignUpCtrl',[
    'vault.services.UtilityService'
]));

(function () {
    'use strict';
    angular.module('vault.features.safes')
        .factory('safesService', safesService);

    function safesService(SAFES_CONSTANTS, $http, RestEndpoints, SessionStore, $q, Modal, Authentication) {
        var service = {
            getSafeTabs: getSafeTabs,
            getSafesNavTags: getSafesNavTags,
            parseSafes: parseSafes,
            createFolder: createFolder,
            saveFolder: saveFolder,
            deleteFolder: deleteFolder,
            getFolderContent: getFolderContent,
            itemIsValidToSave: itemIsValidToSave
        };

        function getFolderContent(path) {
            var url = RestEndpoints.baseURL + '/readAll?path=' + path;
            return $http({
                method: 'GET',
                url: url,
                headers: getHeaders()
            })
                .then(function (response) {
                    var contents = [];
                    response.data.children.map(function (element) {
                        if (element.type === 'secret') {
                            var secrets = JSON.parse(element.value);
                            Object.keys(secrets.data).forEach(function (key) {
                                if (key === 'default' && secrets.data['default'] === 'default') return;
                                contents.push({
                                    type: 'secret',
                                    id: key,
                                    key: key,
                                    value: secrets.data[key],
                                    parentId: element.parentId
                                })
                            })
                        } else if (element.type === 'folder') {
                            contents.push(element);
                        }
                    });
                    response.data.children = contents;
                    return response.data;
                }).catch(catchError);
        }

        function createFolder(path) {
            var url = RestEndpoints.baseURL + '/v2/sdb/createfolder?path=' + path;
            return $http({
                method: 'POST',
                url: url,
                headers: getHeaders()
            })
                .then(function (response) {
                    return response.data;
                })
                .catch(catchError);
        }

        function deleteFolder(path) {
            var url = RestEndpoints.baseURL + '/v2/sdb/delete?path=' + path;
            return $http({
                method: 'DELETE',
                url: url
            })
                .then(function (response) {
                    return response.data;
                })
                .catch(catchError);
        }

        function saveFolder(folderContent, newSecret) {
            // newSecret = {key: 'string', value: 'string'}
            var url = RestEndpoints.baseURL + '/v2/write?path=' + folderContent.id;
            var content = folderContent.children.slice(0);
            if (newSecret) {
                content.push(newSecret);
            }
            var data = parseFolderContentToSecrets(content);
            return $http({
                method: 'POST',
                url: url,
                data: {
                    path: folderContent.id,
                    data: data
                },
                headers: getHeaders()
            }).catch(catchError);
        }

        function itemIsValidToSave(item, index, parent) {
            //SECRET MISSING INPUT
            if (!item.key || !item.value) {
                return Modal.createModalWithController('stop.modal.html', {
                    title: 'Unable to complete action',
                    message: 'Form is missing one or more data fields.'
                });
            }

            //SECRET DUPLICATE KEY
            var otherWithSameName = parent.children.find(function (childItem, position) {
                if (position === index) return false;
                var comparator = (childItem.type === 'folder') ?
                    childItem.id.split('/').pop() : childItem.key;
                return comparator === item.key;
            });

            if (otherWithSameName) {
                return Modal.createModalWithController('stop.modal.html', {
                    title: 'Key already exists.',
                    message: 'This folder already contains an item with the specified name. You can\'t store two secrets with the same key. Please try a different name for the key.'
                });
            }

            //SECRET CONTAINS NEWLINE
            var regex = /\n|\r/gm;
            if (!!item.value.match(regex)) {
                return Modal.createModalWithController('stop.modal.html', {
                    title: 'Error',
                    message: 'Line-break characters are not allowed in Safe Value.'
                });
            }
            return $q.when(true);
        }

        function getSafeTabs() {
            return SAFES_CONSTANTS.SAFE_TYPES
                .map(function (safeType) {
                    return {
                        name: safeType.tabLabel,
                        id: safeType.key
                    }
                })
        }

        function getSafesNavTags() {
            return [{
                displayName: 'SAFES',
                navigationName: 'safes',
                addComma: false,
                show: true
            }, {
                displayName: 'ADMIN',
                navigationName: 'admin',
                addComma: false,
                show: (JSON.parse(SessionStore.getItem("isAdmin")))
            }, {
                displayName: 'DOCUMENTATION',
                navigationName: 'documentation',
                addComma: false,
                show: true,
                redirectTo: function () {
                    var address = RestEndpoints.baseURL + '/swagger-ui.html';
                    var link = document.createElement('a');
                    link.setAttribute('href', address);
                    link.setAttribute('target', '_blank');
                    link.click();
                }
            }];
        }

        function parseSafes(safeListObject) {
            return safeListObject
                .map(function (safeObject) {
                    var entry = Object.entries(safeObject);
                    return {
                        safe: entry[0][0],
                        name: entry[0][0],
                        auth: entry[0][1]
                    }
                })
        }

        function parseFolderContentToSecrets(folderContentChildren) {
            var secretsData = {};
            folderContentChildren.forEach(function (item) {
                if (item.type === 'secret') {
                    var key = item.key;
                    var value = item.value;
                    secretsData[key] = value;
                }
            });
            return secretsData;
        }

        function getHeaders() {
            return {
                'Content-Type': 'application/json',
                'vault-token': SessionStore.getItem('myVaultKey')
            }
        }

        function catchError(error) {
            console.log(error);
            return $q.reject(error);
        }

        return service;
    }
})();
(function () {
  "use strict";
  angular.module('vault.features.safes')
    .config(config);

  function config($stateProvider) {

    $stateProvider
      .state('safes-tabs', {
        url: '/safes-tabs',
        parent: 'base',
        resolve: {
          auth: function () {

          }
        },
        views: {
          'content@base': {
            templateUrl: 'app/Features/safes/safes-tabs/safes-tabs.html',
            controller: 'safesTabsController'
          }
        }
      })
      .state('safes', {
        url: '/safes',
        parent: 'safes-tabs',
        params: {
          type: 'users'
        },
        resolve: {
          safes: function (SessionStore, $q, $state) {
            return JSON.parse(SessionStore.getItem('accessSafes'));
          }
        },
        views: {
          'content@safes-tabs': {
            templateUrl: 'app/Features/safes/safes-tiles/safes-tiles.html',
            controller: 'safesTilesController as vm'
          }
        }
      })
      .state('safes-folders', {
        url: '/safes/folders/:path',
        parent: 'safes-tabs',
        resolve: {
          folderContent: function (safesService, SafesManagement, $state, $stateParams, $q, $timeout) {
            return safesService.getFolderContent($stateParams.path)
              .catch(function (error) {
                return error;
              });
          },
          writeAccess: function (folderContent, SessionStore) {
            if(!folderContent.id) return;
            var safeType = folderContent.id.split('/')[0];
            var safeName = folderContent.id.split('/')[1];
            var safesOfType = JSON.parse(SessionStore.getItem('accessSafes'))[safeType];
            var writeAccess = safesOfType.find(function (safeObj) {
              return safeObj[safeName];
            })[safeName];
            return (writeAccess === 'write');
          }
        },
        params: {
          path: ''
        },
        views: {
          'content@safes-tabs': {
            templateUrl: 'app/Features/safes/safes-folders/safes-folders.html',
            controller: 'safesFoldersController as vm'
          }
        }
      })
  }
})();
